const colorTab = document.getElementById('colorTab')
// const colorPath = 'colorpage'
const fontTab = document.getElementById('fontTab')
// const fontPath = 'fontpage'

const _handleClick = () => {
  if (window.location.pathname.indexOf(colorTab) > -1) {
    colorTab.className += 'active'
  } else if (window.location.pathname.indexOf(fontTab) > -1) {
    fontTab.className += 'active'
  }
}

window.onload = _handleClick



